.signup--title {
  text-transform: capitalize;
  text-align: center;
}

.signup--btn-container {
  display: flex;
  justify-content: flex-end;
}

.signup-join--club {
  background-color: var(--beige);
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  padding-top: 1rem;
  padding-left: 1rem;
}

.btn--club {
  background-color: var(--white);
  border: solid 1px var(--light-green);
  padding: 0.5rem 1rem;
  outline: none;
  margin-right: 1rem;
  margin-bottom: 1rem;
}

.btn--club:focus {
  background-color: var(--dark-green);
  color: var(--white);
}
